 14px;\n        line-height: 16px;\n}\n    /* .announcements-table tr:last-child {\n        width: 96.5%;\n        position: fixed;\n        background: #FFFFFF;\n    } */\n.announcements-table .thead[data-v-7d169136] {\n        background: #F5F5F5;\n        color: #616161;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n}\n.view-btn[data-v-7d169136] {\n        background: none;\n        border:none;\n        color: #2C5A86;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n}\n.items-per-page[data-v-7d169136] {\n        background: #FFFFFF;\n        border: 1px solid #BCBCBC;\n        border-radius: 4px;\n        width: 84px;\n        padding: 7px 12px;\n        margin-left: 12px\n}\n.page-number[data-v-7d169136] {\n        padding: 8px 12px;\n        color: #616161;\n        font-size: 14px;\n        line-height: 16px;\n        background: none;\n        border: none\n}\n.page-number.active[data-v-7d169136] {\n        background: #151233;\n        border-radius: 4px;\n        color: #FFFFFF;\n}\n.change-page-number[data-v-7d169136] {\n        border: none;\n        background: none;\n}\n.change-page-number i[data-v-7d169136] {\n        color: #151233; \n        font-size: 20px; \n        line-height: 20px;\n}\n.remove-btn[data-v-7d169136] {\n        background: none;\n        border:none;\n        color: #FF6B6B;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n}\n.create-announcement[data-v-7d169136] {\n        background: #2C5A86; \n        border-radius: 4px; \n        border: none; \n        padding: 8px 16px;\n        font-size: 14px; \n        font-weight: 500; \n        color: #FFFFFF;\n        display: flex;\n        flex-direction: row;\n        align-items: center;\n        height: 32px;\n        margin-left: 20px;\n}\n.announcement-name[data-v-7d169136] {\n        padding: 8px;\n        background: #D0D1E9;\n        display: flex;\n        justify-content: center;\n        border-radius: 4px;\n        white-space: nowrap;\n        color: #2F3160;\n        font-weight: 500;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/backoffice/announcement/announcementList.vue"],"names":[],"mappings":";AAyLI;QACI,qBAAqB;AACzB;IACA;;OAEG;AACH;QACI,gBAAgB;QAChB,eAAe;QACf,iBAAiB;QACjB,cAAc;QACd,mBAAmB;AACvB;AACA;QACI,cAAc;QACd,aAAa;QACb,mBAAmB;AACvB;AACA;QACI,uBAAuB;QACvB,oCAAoC;QACpC,6BAA6B;QAC7B,YAAY;QACZ,aAAa;QACb,mBAAmB;AACvB;AACA;QACI,uBAAuB;QACvB,YAAY;AAChB;AACA;QACI,aAAa;AACjB;AACA;QACI,cAAc;QACd,eAAe;QACf,gBAAgB;AACpB;AACA;QACI,mBAAmB;QACnB,yBAAyB;QACzB,8CAA8C;QAC9C,mBAAmB;QACnB,WAAW;AACf;AACA;QACI,iCAAiC;AACrC;AACA;QACI,yBAAyB;AAC7B;AACA;QACI,gCAAgC;QAChC;AACJ;AACA;QACI,gCAAgC;QAChC,kBAAkB;QAClB,cAAc;QACd,eAAe;QACf,iBAAiB;QACjB,uBAAuB;QACvB,gBAAgB;QAChB,mBAAmB;QACnB,eAAe;AACnB;AACA;QACI,gCAAgC;QAChC,kBAAkB;QAClB,cAAc;QACd,eAAe;QACf,iBAAiB;AACrB;IACA;;;;OAIG;AACH;QACI,mBAAmB;QACnB,cAAc;QACd,gBAAgB;QAChB,eAAe;QACf,iBAAiB;AACrB;AACA;QACI,gBAAgB;QAChB,WAAW;QACX,cAAc;QACd,gBAAgB;QAChB,eAAe;QACf,iBAAiB;AACrB;AACA;QACI,mBAAmB;QACnB,yBAAyB;QACzB,kBAAkB;QAClB,WAAW;QACX,iBAAiB;QACjB;AACJ;AACA;QACI,iBAAiB;QACjB,cAAc;QACd,eAAe;QACf,iBAAiB;QACjB,gBAAgB;QAChB;AACJ;AACA;QACI,mBAAmB;QACnB,kBAAkB;QAClB,cAAc;AAClB;AACA;QACI,YAAY;QACZ,gBAAgB;AACpB;AACA;QACI,cAAc;QACd,eAAe;QACf,iBAAiB;AACrB;AACA;QACI,gBAAgB;QAChB,WAAW;QACX,cAAc;QACd,gBAAgB;QAChB,eAAe;QACf,iBAAiB;AACrB;AACA;QACI,mBAAmB;QACnB,kBAAkB;QAClB,YAAY;QACZ,iBAAiB;QACjB,eAAe;QACf,gBAAgB;QAChB,cAAc;QACd,aAAa;QACb,mBAAmB;QACnB,mBAAmB;QACnB,YAAY;QACZ,iBAAiB;AACrB;AACA;QACI,YAAY;QACZ,mBAAmB;QACnB,aAAa;QACb,uBAAuB;QACvB,kBAAkB;QAClB,mBAAmB;QACnB,cAAc;QACd,gBAAgB;AACpB","sourcesContent":["<script setup lang=\"ts\">\n    import { container } from \"tsyringe\";\n    import { computed, defineAsyncComponent, ref, watchEffect } from \"vue\";\n    import { ITags } from \"../../../../../../../common/qCloudTemp/quickCloud\";\n    import { IDialog } from \"../../../../../domain/presentation/IDialog\";\n    import { IRouter } from \"../../../../../domain/presentation/IRouter\";\n    import { IBackofficeUseCase } from \"../../../../../domain/useCase/IBackofficeUseCase\";\n    import { IUseCaseExecutor } from \"../../../../../domain/useCase/IUseCaseExecutor\";\n    import CustomSelect from \"../../customSelect.vue\"\n    import { IBoAnnouncementUseCase } from \"../../../../../domain/useCase/backoffice/IBoAnnouncementUseCase\";\n    import { IAnnouncementServiceObj } from \"../../../../../../../common/qCloudTemp/announcement\";\n\n\n    interface IAnnouncementListProps {\n        announcements: Array<IAnnouncementServiceObj>;\n    }\n\n    const props = defineProps<IAnnouncementListProps>();\n    const announcements = computed(() => props.announcements);\n    const executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n    const boAnnouncementUseCase = container.resolve<IBoAnnouncementUseCase>(IBoAnnouncementUseCase);\n    const dialog = container.resolve<IDialog>(IDialog);\n\n\n    const emit = defineEmits([\"refreshTable\"]);\n    const searchTag = ref(\"\");\n    const currentPage = ref(1);\n    const paginatedData = ref<Array<IAnnouncementServiceObj>>([]);\n    const selectedItemsPerPage = ref('10');\n    const itemsPerPageOptions = ref(['10', '20', '30', 'All']);\n\n    async function viewAnnouncement(announcementId: string) {\n        await executor.execute( async () => { await boAnnouncementUseCase.openAnnouncementDetail(announcementId); }, {loading: true});\n    }\n    async function deleteAnnouncement(announcementId: string) {\n        await executor.execute( async () => { await boAnnouncementUseCase.deleteAnnouncement(announcementId); }, {loading: true})\n    }\n    function refreshTable() {\n        emit(\"refreshTable\");\n    }\n    function openAddOrUpdateAnnouncementDialog(announcement?: IAnnouncementServiceObj) {\n        const closeCb = (reason: \"close\" | \"outclick\" | \"component\") => {\n            if(reason == \"component\") {\n                refreshTable();\n            }\n        }\n        if(announcement) {\n            dialog.showDialog(\n                defineAsyncComponent(() => import(`../../dialogs/backoffice/addOrUpdateAnnouncement.vue`)),\n                { closable: true, title: \"Update Announcement\", width: \"542px\", maxHeight: \"379px\", closeCb },{announcement}\n            );\n        }\n        else {\n            dialog.showDialog(\n                defineAsyncComponent(() => import(`../../dialogs/backoffice/addOrUpdateAnnouncement.vue`)),\n                { closable: true, title: \"Create New Announcement\", width: \"542px\", maxHeight: \"379px\", closeCb }\n            );\n        }\n    }\n    function formatDate(strDate: string) {\n        const date = new Date(strDate);\n        const formatter = new Intl.DateTimeFormat('en-GB', { year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', minute: 'numeric' });\n        const formattedDate = formatter.format(new Date(date));\n        return formattedDate;\n    }\n    function getAnnouncementList() {\n        return announcements.value.filter((announcement: IAnnouncementServiceObj) => announcement.text.toUpperCase().includes(searchTag.value.toUpperCase()) || announcement.id.toUpperCase().includes(searchTag.value.toUpperCase()));\n    }\n\n    const totalPages = computed(() => {\n        if(selectedItemsPerPage.value != 'All') {\n            return Math.ceil(getAnnouncementList().length / parseInt(selectedItemsPerPage.value))\n        }\n        else {\n            return 1;\n        }\n    });\n\n    const displayedPageNumbers = computed(() => {\n        const range = 2; // Number of page numbers to display on each side of the current page\n        let startPage = Math.max(1, currentPage.value - range);\n        let endPage = Math.min(totalPages.value, currentPage.value + range);\n        if (endPage - startPage < 2 * range) {\n        // Adjust page numbers to always show the same number of items\n        if (startPage === 1) {\n            endPage = Math.min(endPage + (2 * range - (endPage - startPage)), totalPages.value);\n        } else if (endPage === totalPages.value) {\n            startPage = Math.max(startPage - (2 * range - (endPage - startPage)), 1);\n        }\n        }\n        return Array.from({ length: endPage - startPage + 1 }, (_, index) => startPage + index);\n    });\n\n    watchEffect(() => {\n        if(selectedItemsPerPage.value != 'All') {\n            const startIndex = (currentPage.value - 1) * parseInt(selectedItemsPerPage.value);\n            const endIndex = startIndex + parseInt(selectedItemsPerPage.value);\n            const newData = getAnnouncementList().slice(startIndex, endIndex);\n            paginatedData.value = newData;\n        }\n        else {\n            paginatedData.value = getAnnouncementList();\n        }\n    });\n    function setCurrentPage(page: number) {\n        currentPage.value = page;\n    }\n    function onItemsPerPageSelect (itemCount: string) {\n        selectedItemsPerPage.value = itemCount;\n        setCurrentPage(1)\n    }\n    watchEffect(() => {\n        currentPage.value = 1;\n    });\n\n</script>\n\n<template>\n    <div class=\"page-title\">\n        <span>Announcement List ({{ announcements.length }})</span>\n    </div>\n    <div class=\"filter-area\">\n        <div class=\"search-in-table\">\n            <i class=\"mdi mdi-magnify\"></i>\n            <input class=\"search-input\" type=\"text\" placeholder=\"Search\" alt=\"search\" v-model=\"searchTag\" @input=\"setCurrentPage(1)\"/>\n        </div>\n        <button class=\"create-announcement\" @click=\"openAddOrUpdateAnnouncementDialog()\"><i class=\"mdi mdi-plus\" style=\"font-size: 16px; margin-right: 4px\"></i> CREATE</button>\n    </div>\n    <div style=\"padding-bottom: 50px;\" class=\"table-container\">\n        <table class=\"announcements-table\" id=\"announcements-table\">\n            <tr class=\"thead\">\n                <th class=\"col-xl-1\">Text</th>\n                <th class=\"col-xl-1\">Date Created</th>\n                <th class=\"col-xl-1\">Created By</th>\n                <th class=\"col-xl-1\">Display Type</th>\n                <!-- <th class=\"col-xl-1\">Date Updated</th> -->\n                <!-- <th class=\"col-xl-1\">Updated By</th> -->\n                <th class=\"col-xl-1\">Start Date</th>\n                <th class=\"col-xl-1\">End Date</th>\n                <th class=\"col-xl-1\">Active</th>\n                <th class=\"col-xl-1\"></th>\n            </tr>\n            <tr v-for=\"announcement in paginatedData\" :key=\"announcement.id\">\n                <td :title=\"announcement.text\" style=\"max-width: 200px;\">{{ announcement.text }}</td>\n                <td>{{ formatDate(announcement.createDate) }}</td>\n                <td>{{ announcement.createdBy }}</td>\n                <td>{{ announcement.displayType == \"headerBar\" ? \"Header Bar\": \"Dialog\" }}</td>\n                <!-- <td>{{ announcement.updateDate ? formatDate(announcement.updateDate) : '-' }}</td>\n                <td>{{ announcement.updatedBy ? announcement.updatedBy : '-' }}</td> -->\n                <td>{{ announcement.startDate ? formatDate(announcement.startDate) : '-' }}</td>\n                <td>{{ announcement.endDate ? formatDate(announcement.endDate) : '-' }}</td>\n                <td>{{ announcement.isActive == true ? 'Active': 'Not Active' }}</td>\n                <td><div style=\"display: flex; justify-content: end\">\n                        <button class=\"remove-btn\" @click=\"deleteAnnouncement(announcement.id)\">DELETE</button>\n                        <button class=\"view-btn\" @click=\"openAddOrUpdateAnnouncementDialog(announcement)\">UPDATE</button>       \n                    </div>\n                </td>\n            </tr>\n            <tr>\n                <td colspan=\"2\">\n                    <div style=\"display: flex; align-items: center;\">\n                        <span style=\"font-size: 14px; color: #616161\">Items per page:</span>\n                        <CustomSelect :options=\"itemsPerPageOptions\" @input=\"onItemsPerPageSelect\" class=\"items-per-page\" :selected-class=\"'item-count'\" :default=\"selectedItemsPerPage\"></CustomSelect>\n                    </div>\n                </td>\n                <td colspan=\"4\" style=\"text-align: center; font-weight: 500;\">\n                    <span v-if=\"searchTag.length > 0\">{{ getAnnouncementList().length }} items found</span>\n                </td>\n                <td colspan=\"2\">\n                    <div style=\"float: right; display: flex; align-items: center;\">\n                        <button class=\"change-page-number\" @click=\"currentPage--\" :disabled=\"currentPage === 1\">\n                            <i class=\"mdi mdi-chevron-left\" :style=\"{color: currentPage === 1 ? '#BCBCBC' : ''}\"></i>\n                        </button>\n                        <button v-for=\"pageNumber in displayedPageNumbers\" :key=\"pageNumber\" @click=\"setCurrentPage(pageNumber)\" :class=\"currentPage === pageNumber ? 'page-number active' : 'page-number'\"> {{ pageNumber }} </button>\n                        <button class=\"change-page-number\" @click=\"currentPage++\" :disabled=\"currentPage === totalPages\">\n                            <i class=\"mdi mdi-chevron-right\" :style=\"{color: currentPage === totalPages ? '#BCBCBC' : ''}\"></i>\n                        </button>\n                    </div>\n                </td>\n            </tr>\n        </table>\n    </div>\n</template>\n\n<style scoped>\n    * {\n        font-family: \"Roboto\";\n    }\n    /* .table-container {\n      overflow-x: auto;\n    } */\n    .page-title {\n        font-weight: 500;\n        font-size: 24px;\n        line-height: 28px;\n        color: #151233;\n        margin-bottom: 20px;\n    }\n    .filter-area {\n        margin: 20px 0;\n        display: flex;\n        align-items: center;\n    }\n    .search-in-table {\n        background: transparent;\n        border: 1px solid #BCBCBC !important;\n        border-radius: 4px !important;\n        height: 32px;\n        display: flex;\n        align-items: center;\n    }\n    .search-input {\n        background: transparent;\n        border: none;\n    }\n    .search-input:focus {\n        outline: none;\n    }\n    .search-in-table i {\n        color: #BCBCBC;\n        font-size: 20px;\n        padding: 0px 4px;\n    }\n    .announcements-table {\n        background: #FFFFFF;\n        border: 1px solid #E3E3E3;\n        box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n        border-radius: 10px;\n        width: 100%;\n    }\n    td:hover {\n        background-color: none !important;\n    }\n    table tr:hover:not(:last-child):not(:first-child) {\n        background-color: #EEEEEE;\n    }\n    .announcements-table th {\n        border-bottom: 1px solid #E3E3E3;\n        padding: 18px 27px\n    }\n    .announcements-table tr:not(:last-child) td {\n        border-bottom: 1px solid #E3E3E3;\n        padding: 12px 27px;\n        color: #212121;\n        font-size: 14px;\n        line-height: 16px;\n        text-overflow: ellipsis;\n        overflow: hidden;\n        white-space: nowrap;\n        cursor: default;\n    }\n    .announcements-table tr:last-child td {\n        border-bottom: 1px solid #E3E3E3;\n        padding: 12px 36px;\n        color: #212121;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    /* .announcements-table tr:last-child {\n        width: 96.5%;\n        position: fixed;\n        background: #FFFFFF;\n    } */\n    .announcements-table .thead {\n        background: #F5F5F5;\n        color: #616161;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .view-btn {\n        background: none;\n        border:none;\n        color: #2C5A86;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .items-per-page {\n        background: #FFFFFF;\n        border: 1px solid #BCBCBC;\n        border-radius: 4px;\n        width: 84px;\n        padding: 7px 12px;\n        margin-left: 12px\n    }\n    .page-number {\n        padding: 8px 12px;\n        color: #616161;\n        font-size: 14px;\n        line-height: 16px;\n        background: none;\n        border: none\n    }\n    .page-number.active {\n        background: #151233;\n        border-radius: 4px;\n        color: #FFFFFF;\n    }\n    .change-page-number {\n        border: none;\n        background: none;\n    }\n    .change-page-number i {\n        color: #151233; \n        font-size: 20px; \n        line-height: 20px;\n    }\n    .remove-btn {\n        background: none;\n        border:none;\n        color: #FF6B6B;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .create-announcement {\n        background: #2C5A86; \n        border-radius: 4px; \n        border: none; \n        padding: 8px 16px;\n        font-size: 14px; \n        font-weight: 500; \n        color: #FFFFFF;\n        display: flex;\n        flex-direction: row;\n        align-items: center;\n        height: 32px;\n        margin-left: 20px;\n    }\n    .announcement-name {\n        padding: 8px;\n        background: #D0D1E9;\n        display: flex;\n        justify-content: center;\n        border-radius: 4px;\n        white-space: nowrap;\n        color: #2F3160;\n        font-weight: 500;\n    }\n</style>\n<style>\n    .items-per-page .items {\n        color: #212121;\n        border-radius: 8px;\n        border: 1px solid #E3E3E3;\n        overflow: hidden;\n        background-color: #FFFFFF;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n        margin-top: 8px;\n    }\n    .items-per-page .items .option {\n        color: #212121;\n        padding-left: 12px;\n        line-height: 32px;\n    }\n    .items-per-page .items .option:hover{\n        background-color: #EEEEEE;\n    }\n    .items-per-page i {\n        font-size: 16px;\n        color: #BCBCBC;\n    }\n</style>"],"sourceRoot":""}]);
